<template>
	<view class="container">
		<ClassTitle tit="中职学校" path="/pages/school/school" />
		<SchoolList :schoolData="schoolListFn" />
		<ClassMore path="/pages/school/school" />
	</view>
</template>

<script>
	import ClassTitle from '../library/ClassTitle.vue'
	import SchoolList from '../SchoolList.vue'
	import {
		getSchoolList
	} from '@/api/school.js'

	import {
		websiteUrl
	} from '@/utils/config.js'
	import ClassMore from '@/components/library/ClassMore.vue'
	export default {
		name: 'HemeSchool',
		components: {
			ClassTitle,
			ClassMore,
			SchoolList
		},
		data() {
			return {
				schoolData: [],
			}
		},
		created() {
			getSchoolList().then(res => {
				this.schoolData = res.data.rows
			})
		},
		computed: {
			schoolListFn() {
				return this.schoolData.map(item => {
					return {
						title: item.title,
						titlepic: websiteUrl + item.titlepic,
						id: item.id,
						xuexiaoleixing: item.xuexiaoleixing,
						tagsArr: [item.xuexiaodiqu, ...item.xuexiaoxingzhi.split('|'), ...item.pttags.split('|')]
							.filter(item => item.length > 0)
					}
				})
			}


		}
	}
</script>

<style scoped lang="scss">
	.container {
		margin: 20px 0;
	}
</style>
